.login-wrapper,
	.login-wrapper > .container-fluid,
	.login-wrapper > .container-fluid > .row{height: 100%;}
	.login-wrapper > .container-fluid{display: table;}
	.login-wrapper > .container-fluid > .row{display: table-row;}
	.login-wrapper > .container-fluid > .row > div{display: table-cell; width: 50%;}
	.login-wrapper > .container-fluid > .row > div:first-child{background-color: #f2f2f2;  text-align: center; vertical-align: middle; }
	.login-wrapper > .container-fluid{padding: 0;}
	
	/* login-form-wrapper */
	.login-form-wrapper{padding:65px 50px 50px 50px;}
	.login-form-header{padding-bottom: 30px;}
	.login-form-header > h1{font-size:25px; font-weight: 800; margin: 0;}
	.login-form-header > p{font-size: 12px; font-weight: 400; margin: 10px 0 0 0;}
	.login-form-wrapper .btn{min-width: 180px;}
	.login-form-nested-wrapper .form-group{margin-bottom:35px;}
	.form-group > h5{color: green;}
	.form-group > h6{color: red;}
	.login-links-wrapper{font-size: 14px;}
	.login-links-wrapper{margin-top: 20px;}
	.login-links-wrapper a{color: #000;}
	.login-links-wrapper a:hover{color: rgba(0,0,0,.80); text-decoration: none;}
	.login-links-extra-links{display: -webkit-box; display: -moz-box; display: -ms-flexbox; display: -webkit-flex; display: flex; -webkit-box-align: center; -moz-box-align: center; -ms-flex-align: center; -webkit-align-items: center; align-items: center; justify-content: space-between; -webkit-justify-content: space-between; -ms-justify-content: space-between; -moz-justify-content: space-between;}
	.remember-checkbox{display: -webkit-box; display: -moz-box; display: -ms-flexbox; display: -webkit-flex; display: flex; -webkit-box-align: center; -moz-box-align: center; -ms-flex-align: center; -webkit-align-items: center; align-items: center;}
	.remember-checkbox label{margin: 0 0 0 10px; font-size: 14px;}
	.login-fields-spacing > form > div:first-child{margin-bottom:25px;}
	.find-your-business-wrapper h2{font-size:20px; font-weight: 800; margin: 0;}
	.find-your-business-wrapper p{font-size: 10px; color:#808080; margin: 10px 0 30px 0;}
	.form-control{font-family: 'Open Sans', sans-serif; height: 38px; font-size: 14px !important; border-color:#c9c9c9; border-radius: 0; -webkit-border-radius: 0; -ms-border-radius: 0; font-size: 16px;}
	.form-control:focus{ color: #495057; background-color: #fff; border-color: #000; outline: 0; box-shadow: 0 0 8px 0.2rem rgba(0,0,0,.25);}
	
	.btn-primary{border-color: #000; background-color: #000;}
	.btn{font-size:16px; font-family: 'Open Sans', sans-serif;}
	.btn-primary:hover,
	.btn-primary:focus,
	.btn-primary:not(:disabled):not(.disabled):active,
	.btn-primary:not(:disabled):not(.disabled):active:focus{border-color: #1d1d1d; background-color: #1d1d1d; box-shadow:none;}
	.cover-image{background-position: center; background-size: cover;}
	/* responsive start here */
	@media(min-width:1030px){
		.login-form-nested-wrapper{width: 385px;}
	}
	
	@media(max-width:899px){
		.login-links-extra-links{display: block;}
		.login-links-extra-links a{display: block;}
		.login-links-extra-links a + a{margin-top:10px;}
		.login-form-wrapper { padding: 35px 30px 30px 30px;}  
		.login-form-nested-wrapper .form-group{margin-bottom: 25px;}
		.login-form-header{padding-bottom:20px;}
	}
	@media(max-width:640px){
		.login-wrapper > .container-fluid > .row > div{display: block; width: 100%; flex: 0 0 100%; max-width:100%;} 
		.login-wrapper > .container-fluid > .row > div{height: 40%;}
		.login-wrapper > .container-fluid > .row > div:last-child{height:60%}
	}